/*Estilos LoginPage*/
.container-loginn {
  background-color: var(--color-primario);
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* margin-top: 10vh;  */
}
.container-login{
	/* margin-top: 10vh; */
  padding: 38px;
  width: 380px;
  border: 1px solid var(--color-border);
  border-radius: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-login {
  color: var(--color-primario);
  width: var(--width-input);
  height: 30px;
  border: none;
  background: var(--color-segundario);
  border-radius: 8px;
  margin-top: .8rem;
  margin-bottom: 1rem;
  cursor: pointer;
}

form input {
  border: none;
}

form .login-form {
  margin-top: 15px;
  width: var(--width-input);
  height: 35px;
  border: 1px solid var(--color-border);
  border-radius: 8px;
  display: flex;
  align-items: center;
}
h4 {
  font-size: 1rem;
  line-height: 25px;
  letter-spacing: -0.035em;
  margin: 1rem 0rem;
  color: var(--font-color-primario);
}

.sub-text {
  font-size: 0.9rem;
  margin-bottom: 1rem;
}
.login-title{
	padding-bottom: 1rem;
  font-size: 1.3rem;
}
span {
	margin-left: 3px;
  color: #2d9cdb;
 
}
a{
	text-decoration: none;
}

.text-2 {
  margin-top: 1rem;
  font-size: 14px;
  color: var(--color-icon);
}

/* ICON STYLE */
.container-icon {
  display: flex;
}

.icon-style {
  border: 1px solid var(--color-border);
  color: var(--color-icon);
  padding: 10px 14px;
  border-radius: 30px;
  margin: 1rem;
}
.icon-form {
  margin-left: 10px;
  color: var(--color-icon);
}

.p-danger {
  display: inline-block;
  text-align: start;
  align-items: flex-start;
  color: var(--btn-logout);
  font-size: 11px;
}
